import React from "react";
import { Link, graphql } from "gatsby";
import styled from "@emotion/styled";
import { css } from "@emotion/core";

import SEO from "../components/seo";

const Title = styled.a`
  font-family: Montserrat, 'Avenir Next', 'Helvetica Neue', sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
  user-select: none;
  box-shadow: none;
`;

const Introduce = styled.div`
  padding: 0;
  margin: 10vh 0 0;
  user-select: none;
  text-rendering: optimizelegibility;
  font-family: Montserrat, 'Avenir Next Condensed', 'Helvetica Neue', sans-serif;
  font-size: calc(18px + 1.8vw);
  font-weight: bold;
  line-height: 1.2;
`;

const Row = styled.span`
  background-color: #000;
  color: #fff;
  padding: 0 4px;

  a {
    display: inline-block;
    font-weight: bold;
    color: #ffa966;
    padding: 0 0.25em;
    text-decoration: none;
    box-shadow: none;

    &:hover,
    &:active,
    &:focus {
      text-decoration: none;
    }
  }
`;

const Blank = styled.div`
  height: 0.5em;
`;

export default class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title

    return (
      <div css={css`
        background-color: #e6f000;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        overflow: auto;
        display: flex;
        flex-direction: column;
      `}>
        <SEO title="M." />
        <div css={css`
          text-align: right;
          padding: 40px 40px 0 40px;
          line-height: 1;
        `}>
          <Title href="/">
            {siteTitle}
          </Title>
        </div>
        <Introduce>
          <Row>Hi, there. 我是 p10y (performonkey)</Row>
          <br />
          <Blank />
          <Row>我是一名前端工程师</Row>
          <br />
          <Row>我喜欢一切有意思的事物</Row>
          <Blank />
          <Row>在<Link to="/pieces/">这里</Link>我会分享一些好玩的东西</Row>
          <br />
          <Row>希望你能喜欢 :P</Row>
          <Blank />
          <Row>
            <span>你可以在这里找到我</span>
            <a href="https://twitter.com/performonkey" target="_blank" rel="noopener noreferrer">
              Twitter
            </a>,
            <a href="https://github.com/performonkey" target="_blank" rel="noopener noreferrer">
              GitHub
            </a>
          </Row>
          <br />
          <Row>也可以给我发 <a href="mailto:m@nkey.co">Email</a></Row>
        </Introduce>
      </div>
    )
  }
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
